const http = {
  loginTableData() {

  },
  opreateHttp(vm, url, data, opreatetitle, successCall) {
    vm.$validator.validateAll().then(function (value) {
      if (value) {
        vm.$axios
          .post(vm.HOST + url, data)
          .then(function (success) {
            var res = success.data;
            if (res.code == "0") {
              vm.$layer.msg(
                opreatetitle +
                "成功", {
                  icon:1,
                  end:function () {
                    successCall();
                    vm.$layer.close(this.id);
                  }
                });
              data = {};
            } else {
              vm.$layer.msg(res.msg ,{icon:2});
            }
          })
          .catch(function (error) {
            vm.$layer.msg(
              '网络错误，请检查网络是否畅通',{icon:5}
            );
          });
      }
    });
  },
}

export default http;



